// schedule í‘œ ë§Œë“¤ê¸° div
function scheduleFunc() {
  for (let i = 1; i <= 9; i++) {
    const div = document.createElement("div");
    div.classList.add(`scheduleDiv_container_${i}`);
    div.classList.add(`scheduleDiv_container`);
    scheduleContainer.appendChild(div);
    const divContainer = document.querySelector(`.scheduleDiv_container_${i}`);

    for (let j = 1; j <= 5; j++) {
      const div = document.createElement("div");
      div.id = `${dayArr[j - 1]}ìš”ì¼_${i}ì‹œ`;
      divContainer.appendChild(div);

      let IdArr = []; // Idì„ dayì™€ hourë¡œ ë°°ì—´ë¶„ë¦¬
      const getDivId = div.id;
      let sliceGetId = getDivId.slice(0, 3);
      IdArr.push(sliceGetId);
      sliceGetId = getDivId.slice(4, 6);
      IdArr.push(sliceGetId);
      const IdDay = IdArr[0]; // div ì˜ Idì„ Dayë¡œ ë§Œë“  ë³€ìˆ˜
      const IdHour = IdArr[1]; // div ì˜ Idì„ Hourë¡œ ë§Œë“  ë³€ìˆ˜

      const dayStorage = localStorage.getItem(IdArr[0]); // day ê¸°ì¤€ìœ¼ë¡œ storyageì„ ê°€ì ¸ì˜¨ê²ƒ
      const hourStorage = localStorage.getItem(IdArr[1]); // hour ê¸°ì¤€ìœ¼ë¡œ storyageì„ ê°€ì ¸ì˜¨ê²ƒ

      const saveLocal = localStorage.getItem(SUBJECT_ID); // storageì„ KEY : subject ì„ ê¸°ì¤€ìœ¼ë¡œ valueì„ ê°€ì ¸ì˜¨ ê²ƒ
      const parseSaveLocal = JSON.parse(saveLocal); // ê°ì²´ë¡œ ë³€í™˜í•œ saveLocal ê°’
      let arrSaveLocal = Object.entries(parseSaveLocal); // parseSaveLocal ê°ì²´ê°’ì„ ë°°ì—´ë¡œ ë§Œë“  ë°°ì—´
      arrSaveLocal = arrSaveLocal.map((each) => ({
        day: each[1].day,
        hour: each[1].time,
        value: each[1].value,
      }));
      console.log(arrSaveLocal);
      console.log(
        arrSaveLocal.find((e) => {
          return e.day === IdDay && e.hour === IdHour;
        })
      );
      div.innerHTML = `test`;
    }
  }
}

