// subjectArr ëŠ” ì‹œì‘í• ë•Œ í•­ìƒ ì²˜ìŒìœ¼ë¡œ ì •í•´ì ¸ìˆë‹¤. ê·¸ë˜ì„œ ìŠ¤í† ë¦¬ì§€ì— ìˆëŠ”ê±¸ ë°°ì—´ì— ë„£ëŠ”ë‹¤.
